body {
  background: linear-gradient(135deg, #74ebd5, #4e68da);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 2rem;
  font-family: Segoe UI, sans-serif;
  display: flex;
}

.weather-card {
  backdrop-filter: blur(12px);
  text-align: center;
  background: #ffffff26;
  border-radius: 20px;
  width: 320px;
  padding: 2rem;
  animation: .8s ease-in-out fadeIn;
  box-shadow: 0 8px 24px #0003;
}

.weather-card h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.weather-card img {
  width: 100px;
  height: 100px;
}

.weather-list {
  margin-top: 1rem;
  padding: 0;
  list-style: none;
}

.weather-list li {
  margin: 8px 0;
  font-size: 1rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
